        <button @click="previousMove" id="previous" type="button">
          &#60;
        </button>
        <button @click="nextMove" id="next" type="button">
          &#62;
        </button>
        <p class="ticker">{{this.moveIndex +1 }} of {{this.moves.length}}</p>
